.tuyendung-1
	.container
		padding-top: 50px
		padding-bottom: 40px
		background: #fef0e8
		@media (max-width: 576px)
			padding-bottom: 20px
	.title
		font-size: 35px
		line-height: 35px
		margin-bottom: 30px
		font-weight: 700
		color: #333
		text-align: center
		text-transform: uppercase
		@media (max-width: 576px)
			font-size: 24px !important
			line-height: 24px !important
			margin-bottom: 16px !important
	.btn-more
		color: #f26f21
		border-color: #f26f21
		&:hover
			background: #f26f21
			color: #fff
		span
			&:after
				+mtrl('\f35d')
				margin-left: 4px
